import * as React from "react"
import * as styles from "../styles/main-nav.module.scss"
import AFC from "../images/icons/Icon_AFC.png"
import contactUs from "../images/icons/Icon_Contact_Us.svg"
import location from "../images/icons/Icon_Location.svg"
import hours from "../images/icons/Icon_Hours.svg"
import personalTraining from "../images/icons/Icon_Personal_Training.svg"
import athletePerformance from "../images/icons/Icon_Athlete_Performance.svg"
import groupFitness from "../images/icons/Icon_Group_Fitness.svg"
import nutritionCoaching from "../images/icons/Icon_Nutrition_Coach.svg"
import rockClimbing from "../images/icons/Icon_Rock_Climbing.svg"
import meetTheTeam from "../images/icons/Icon_Meet_The_Team.svg"
import blog from "../images/icons/Icon_Blog.svg"
import { Link } from "gatsby"
import * as Icon from "../components/icons"

const MainNav = () => {
    const [subNavActive, setSubNavActive] = React.useState('')

    const nav = [
        {
            icon: AFC,
            label: "Train With The Best",
            slug: "/afc-new/"
        },
        {
            icon: <Icon.ContactUs/>,
            label: "Contact",
            slug: "#"
        },
        {
            icon: <Icon.Location/>,
            label: "Location",
            slug: "#"
        },
        {
            icon: <Icon.Hours/>,
            label: "Hours",
            slug: "#"
        },
        {
            icon: <Icon.PersonalTraining/>,
            label: "Personal Training",
            slug: "/afc-new/personal-training"
        },
        {
            icon: <Icon.AthletePerformance/>,
            label: "Athlete Training",
            slug: "/afc-new/athlete-performance"
        },
        {
            icon: <Icon.GroupFitness/>,
            label: "Group Fitness",
            slug: "/afc-new/group-fitness"
        },
        {
            icon: <Icon.NutritionCoaching/>,
            label: "Nutrition Coaching",
            slug: "/afc-new/nutrition-coaching"
        },
        {
            icon: <Icon.RockClimbing/>,
            label: "Rock Climbing",
            slug: "/afc-new/rock-climbing"
        },
        {
            icon: <Icon.MeetTheTeam/>,
            label: "Meet The Team",
            slug: "/afc-new/meet-the-team"
        },
        {
            icon: <Icon.Blog/>,
            label: "Wellness Blog",
            slug: "/afc-new/wellness-blog"
        }
    ]

    return (
        <div className={styles.mainNavContainer}>
            <nav>
                <ul className={styles.mainNavLeft}>
                    {
                        nav.map((navItem, key) => {
                            if (key === 0) {
                                return (
                                    <li key={key}>
                                        <Link
                                            to={navItem.slug}
                                            className={`${styles.navItem} ${subNavActive.length > 0 ? styles.subNavActive + ' ' : ''}`}
                                            activeClassName={styles.active}
                                            onClick={() => {
                                                setSubNavActive('')
                                            }}
                                        >
                                            <img src={navItem.icon} className={styles.icon} />
                                            <label>{navItem.label}</label>
                                        </Link>
                                    </li>
                                )
                            }
                            else if (key < 4) {
                                return (
                                    <li
                                        className={`
                                            ${styles.navItem}  
                                            ${subNavActive == navItem.label ? styles.subActive : ""}
                                            ${subNavActive.length > 0 ? styles.subNavActive + ' ' : ''}
                                        `}
                                        key={key}
                                        onClick={() => {
                                            setSubNavActive(navItem.label)
                                        }}
                                    >
                                        {/* <img src={navItem.icon} className={styles.icon} /> */}
                                        <div className={styles.icon}>
                                            {navItem.icon}
                                        </div>
                                        <label>{navItem.label}</label>
                                    </li>
                                )
                            }
                        })
                    }
                </ul>

                <div></div>

                <ul className={styles.mainNavRight}>
                    {
                        nav.map((navItem, key) => {
                            if (key > 3 && key <= 10) {
                                return (
                                    <li key={key}>
                                        <Link
                                            to={navItem.slug}
                                            className={`${styles.navItem} ${subNavActive.length > 0 ? styles.subNavActive + ' ' : ''}`}
                                            activeClassName={styles.active}
                                            onClick={() => {
                                                setSubNavActive('')
                                            }}
                                        >
                                            <div className={styles.icon}>
                                                {navItem.icon}
                                            </div>
                                            <label>{navItem.label}</label>
                                        </Link>
                                    </li>
                                )
                            }
                        })
                    }
                </ul>
            </nav>
        </div>
    )
}

export default MainNav